                ></v-text-field>
              </v-col>
            </v-row>
          </v-container>
          <small>*授权删除后无法恢复</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="blue darken-1"
            text
            @click="dialog = false"
