			<template v-slot:item.avatar="{ item }">
				<v-avatar>
					<img :src="item.avatar" >
				</v-avatar>
			</template>
			<template v-slot:item.actions="{ item }">
  <v-row justify="center">
				<v-btn
					elevation="2"
					@click="edititem(item)"
					:loading="item.editLoading"
					>
					<v-icon left>
						mdi-pencil
					</v-icon>
					更新授权
				</v-btn>
				<v-divider
				class="mx-4"
				vertical
				></v-divider>
				

				<v-btn
					elevation="2"
					@click="dialog = true"
					:loading="item.delLoading"
					>
					<v-icon left>
						mdi-delete
