</template>
<script>
	import axios from "axios";
export default {
	data () {
		return {
			totalDesserts: 0,
			dialog: false,
			desserts: [],
			loading: true,
			options: {},
			headers: [
				{ text: '',value: 'avatar'},
				{ text: '用户名',value: 'name'},
				{ text: '百度uk',value: 'uuid'},
				{ text: '到期时间', value: 'time' },
				{ text: '', value: 'actions' },
			],
		}
	},
	mounted () {
		this.getDataFromApi()
	},
	methods: {
		getDataFromApi () {
			const that=this;
			this.loading = true
			axios.get('https://mac.wcz.pub:8888/baiduapi/get_token?all=1')
				.then(function(res){
					console.log(res.data);
					that.desserts = res.data;
					that.loading = false;
				})
				.catch(function(error){
					console.log(error);
				})
		},
		delitem(item) {
			const that=this;
			console.log(item);
			this.$set(item, 'delLoading', true)
			axios.get('https://mac.wcz.pub:8888/baiduapi/del_token?id='+item.action)
				.then(function(res){
					console.log(res);
					that.$set(item, 'delLoading', false)
					that.getDataFromApi()
				})
				.catch(function(error){
					console.log(error);
					that.$set(item, 'delLoading', false)
				})
		},
		edititem(item) {
			console.log(item);
			this.dialog = false;
		},
		authorization() {
			console.log("authorization");
			if(/phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone/i.test(navigator.userAgent)) {
					window.location.href = "https://openapi.baidu.com/oauth/2.0/authorize?response_type=code&client_id=QPQSfa0ZbGFiYS0fecszVhmpX9fhS8z6&redirect_uri=http://mac.wcz.pub:8888/baiduapi/baiduret&scope=basic,netdisk&display=mobile&force_login=1&state=new";
			} else {
					window.location.href = "https://openapi.baidu.com/oauth/2.0/authorize?response_type=code&client_id=QPQSfa0ZbGFiYS0fecszVhmpX9fhS8z6&redirect_uri=http://mac.wcz.pub:8888/baiduapi/baiduret&scope=basic,netdisk&display=web&force_login=1&state=new";
			}
		},
	},
}
</script>
